@font-face {
    font-family: 'Ownglyph_ParkDaHyun';
    src: url('https://fastly.jsdelivr.net/gh/projectnoonnu/2411-3@1.0/Ownglyph_ParkDaHyun.woff2') format('woff2');
    font-weight: normal;
    font-style: normal;
}

.find-store-container {
    padding: 20px;
}

.restaurant-list {
    display: flex;
    flex-wrap: wrap;
    gap: 20px;
}

.restaurant-card {
    border: 1px solid #ccc;
    padding: 15px;
    width: calc(25% - 20px);
    box-sizing: border-box;
}

.restaurant-card img {
    width: 100%;
    height: 150px;
    object-fit: cover;
}

.pagination {
    margin-top: 20px;
    text-align: center;
}

.pagination button {
    margin: 0 5px;
    padding: 10px 15px;
    border: none;
    background-color: #f1f1f1;
    cursor: pointer;
}

.pagination button.active {
    background-color: #007bff;
    color: #fff;
}